import React, { useEffect, useMemo } from "react";
import { useDispatch, useSelector } from "react-redux";
import DataTable from "../../formElements/DataTable";
import { useNavigate } from "react-router-dom";
import { FaEdit } from "react-icons/fa";

import { FaLongArrowAltLeft } from "react-icons/fa";
import { category, getAllCategory } from "../../../redux/slices/CategorySlice";

const List = () => {
  const Category = useSelector(category);
  const dispatch = useDispatch();
  const navigate = useNavigate();
  useEffect(() => {
    if (Category.length === 0) {
      dispatch(getAllCategory());
    }
  }, []);

  const data = Category.map((Cat) => {
    return {
      id: Cat.id,
      Category: Cat.Category,
      inactive: Cat.inactive === 1 ? "Yes" : "No",
    };
  });
  const columns = useMemo(
    () => [
      {
        Header: "Category",
        accessor: "Category",
      },
      {
        Header: "Inactive",
        accessor: "inactive",
      },
      {
        Header: "Edit",
        accessor: "id", // Assuming 'id' is the unique identifier for your rows
        Cell: ({ row }) => (
          <button
            onClick={() => {
              navigate("../", { state: { Categoryid: row.original.id } });
            }}
          >
            <FaEdit className="hover:text-orange-500" />
          </button>
        ),
      },
    ],
    []
  );

  return (
    <>
      <div className="px-7 py-1 overflow-auto flex flex-col   h-[calc(100vh-70px)]">
        <div className="inline-flex items-center">
          <span
            className=" inline-block p-2  text-slate-500 cursor-pointer hover:bg-slate-700 hover:text-slate-300 hover:rounded-lg "
            onClick={() => navigate("../")}
          >
            <FaLongArrowAltLeft></FaLongArrowAltLeft>
          </span>
          <span className=" text-slate-300 text-sm">Back</span>
        </div>
        {Category.length > 0 ? (
          <DataTable columns={columns} data={data} />
        ) : (
          <p>No Category Found</p>
        )}
      </div>
    </>
  );
};

export default List;
